.recipe-div {
  background: white;
  border-radius: 5px;
  -webkit-box-shadow: 0px 5px 10px rgb(66, 65, 65);
          box-shadow: 0px 5px 10px rgb(66, 65, 65);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 600px auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin:  30px 0px;
  padding:10px 30px;
  width: 40%;
}
.image {
  border-radius: 10px;
  height: 200px;
  width: 100%;
}

.recipe-div h1 {
  font-size: 24px;
  text-align: center;
}

.recipe-div ul li {
  margin-left: 50px;
  text-align: justify;
}

.recipe-div p {
  margin-left: 40px;
  text-align: justify;
}
